import  { FetchAction, FetchActionTypes, WeatherState }  from '../../types/types';

const initialState: WeatherState = {
    error: null,
    loading: false
};

export const weatherReducer = (state = initialState, action: FetchAction): WeatherState => {
    switch(action.type) {
        case FetchActionTypes.FETCH_WEATHER:
            return {
                ...state,
                loading: true
            }
        case FetchActionTypes.FETCH_WEATHER_SUCCESS:
            return {
                ...state,
                loading: false
            }
        case FetchActionTypes.FETCH_WEATHER_ERROR:
            return {
                ...state,
                error: action.payload,
                loading: false
            }
        default:
            return state
    }
}